import common from '../../common/common.js'
import util from '../../common/util.js'
import uniPopup from '../../components/uni-popup.vue'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			userid:'',
			userInfo:[],
			coupon_nums:0,
			ads:[],
			popType: 'middle',
			showPopupMiddle: false,
			info:{
				'bg_color':'255,255,255',
				'bg_alpha':1,
				'delta':1
			},
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			
			qiandao:[],
			today:'',		//今天
			
			canbuy:1,
		}
	},
	onLoad() {
		var that = this
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');
		var qiandao = uni.getStorageSync('qiandao');
		console.log(qiandao,"qiandao")
		
		that.userid = userid
		that.userInfo = userInfo
		
		if(qiandao != ''){
			that.qiandao = qiandao;
		}
		that.today = util.getDateTime();
		console.log(that.today)
		
		that.getCouponNums();
		that.getInviteAds();
		
		
		var currentPages = getCurrentPages()
		that.info.delta = currentPages.length
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		
		console.log("userid",userid);
		console.log("userInfo",userInfo);
	},
	onPullDownRefresh:function(){
	  var that = this
	  uni.showNavigationBarLoading();
	  console.log('下拉刷新中...');	
	  that.getUserInfo();
	  that.getCouponNums();
	  that.getInviteAds();
	  
	  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	methods: {
		//获取优惠券数量
		getCouponNums(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var userid = uni.getStorageSync('userid');
			console.log('getCouponNums')
			console.log("userid",userid)
			uni.request({
				url: common.apiUrl + 'user/getCouponNums',
				method: 'GET',
				data: {
					usreid:userInfo.users_id
				},
				success: res => {
					that.coupon_nums = res.data.nums;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//签到
		userqiandao(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			if(!userInfo){
				that.checkLogin();
				return false
			}
			uni.request({
				url: common.apiUrl + 'user/userqiandao',
				method: 'GET',
				data: {
					userid:userInfo.users_id
				},
				success: res => {
					if(res.data.code == 0){
						uni.showToast({
							title:'签到成功',
							icon:'success',							
						})
						that.qiandao = that.today
						uni.setStorageSync('qiandao',that.qiandao);
						that.getUserInfo();
					}else{
						uni.showToast({title:res.data.msg,icon:'none',duration:1500})
					}					
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupMiddle = false;
		},
		//展示居中 popup
		showMiddlePopup: function() {
			this.hidePopup();
			this.showPopupMiddle = true;
		},
		//邀请广告
		getInviteAds(){
			var that = this
			uni.request({
				url: common.apiUrl + 'ads/getInviteAds',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res)
					that.ads = res.data.ads
					that.canbuy = res.data.canbuy
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//客服
		kefu(){
			var that = this
			uni.showActionSheet({
				itemList: ['在线客服', '15988355159'],
				success: function (res) {
					if(res.tapIndex == 0){
						console.log('在线客服');
						that.showMiddlePopup();
					}
					if(res.tabpIndex == 1){
						uni.makePhoneCall({
							phoneNumber:'114'
						})
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		ruzhu(){
			
			uni.navigateToMiniProgram({
			      appId: 'wx8cac4ba732d41675', // 要跳转的小程序的appid
			      path: 'pages/index/index', // 跳转的目标页面
			      extarData: {
			        open: 'auth'
			      },
			      success(res) {
			        // 打开成功  
					console.log('aaa')
			      },
				  fail(res){
					  console.log(res)
				  }
			}) 
		},
		//链接
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url;
			uni.navigateTo({
				url:url
			})
		},
		//获取用户信息
		getUserInfo(){
			var that = this
			var userid = that.userid
			if(userid > 0){
				uni.request({
					url: common.apiUrl+'user/getUserInfo',
					method: 'GET',
					data: {
						userid:userid
					},
					success: res => {
						if(res.data.code == 0){
							var userInfo = res.data.userInfo;
							uni.setStorageSync('userInfo',userInfo);
							that.userInfo = userInfo
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.showModal({
					title: '提示',
					content: '你还没登录，现在去登录吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if(res.confirm){
							uni.navigateTo({
								url: '/pages/userlogin/userlogin',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		
		//用户登录
		checkLogin(){
			var that = this
			var userid = uni.getStorageSync('userid');
			if(!userid){
				console.log('还没有登录');
				uni.navigateTo({
					url: '/pages/userlogin/userlogin',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}else{
				console.log('已经登录');
			}
		},
		
		//用户信息
		setting(){
			uni.navigateTo({
				url: '/pages/user/setting/setting',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	components: {
		uniPopup
	}
}
